<template>
	<div id="notice">
		<img src="/dist/image/home/close.png" class="close" @click="close"/>
		<div class="notice-wrap">
			<p>APP</p>
			<button @click="know()"></button>
		</div>
	</div>
</template>

<script>
	import config from "../config/config.js";
	export default{
		mounted(){
			
		},
		methods:{
			close(){
				this.$parent.noticeShow = false;
			},
			know(){
				axios.post("/index/ShowMessage",{
					type:2
				},config).then(res => {
					if(res.data.code == 0){
						this.$parent.noticeShow = false;
					}
				})
			}
		}
	}
</script>

<style>
</style>


