<template>
	<div id="talkingNow">
		<v-header></v-header>
		<img v-lazy="$route.params.img" class="detailImg" />
		<div class="talking">
			<div class="talking-top">
				<p></p>
				<p><img src="/dist/image/home/subhome/star_good.png" v-for="n in num" @click="change(n)" /><img src="/dist/image/home/subhome/star_bad.png" v-for="n in (5-num)" @click="change(num+n)"/></p>
				<p>{{num | level}}</p>
			</div>
			<div class="talking-bottom">
				<ul>
					<li @click="add('')"></li>
					<li @click="add('')"></li>
					<li @click="add('')"></li>
					<li @click="add('')"></li>
					<li @click="add('')"></li>
					<li @click="add('')"></li>
				</ul>
				<div class="text-wrap">
					<textarea placeholder="" v-model="value"></textarea>
					<span>{{count}}/50</span>
				</div>
				<!--<div class="img-wrap">
					<div class="addImg">
						<img src="/dist/image/talkingnow/camera.png" />
						<span></span>
					</div>
				</div>-->
			</div>
		</div>
		<v-alert v-if="alertshow" :context="context"></v-alert>
	</div>
</template>

<script>
	import Header from "./header.vue";
	import Alert from "../alert.vue";
	
	export default{
		components:{
			"v-header":Header,
			"v-alert":Alert
		},
		data(){
			return {
				num:0,
				count:0,
				value:"",
				alertshow:false,
				context:""
			}
		},
		methods:{
			change(n){
				this.num = n;
			},
			add(str){
				var temp = this.value;
				temp += str;
				this.value = temp;
			}
		},
		filters:{
			level:function(val){
				switch(val){
					case 0:
					return "";
					case 1:
					return "";
					case 2:
					return "";
					case 3:
					return "";
					case 4:
					return "";
					case 5:
					return "";
				}
			}
		},
		watch:{
			"value":function(){
				
				this.count = this.value.length;
				if(this.count > 50){
					var temp = this.value;
					temp = temp.substring(0,50);
					this.value = temp;
				}
			}
		},
		activated(){
			wx.hideMenuItems({
			  menuList: ["menuItem:copyUrl","menuItem:readMode","menuItem:openWithQQBrowser","menuItem:openWithSafari","menuItem:share:qq","menuItem:share:weiboApp","menuItem:favorite","menuItem:share:facebook","menuItem:share:QZone"] // menu3
			});
		}
	}
</script>

<style>
</style>


