<template>
	<div id="weixin">
		<v-header></v-header>
		<div id="totalTime">
			<p></p>
			<p><span>10</span><span>00</span> </p>
		</div>
		<div id="totalMoney">
			<span></span>
			<span>{{money}}</span>
		</div>
		<div id="methods">
			<p class="choose"></p>
			<p class="weixin">
				<img src="/dist/image/weixin/weixin.png" />
				<span></span>
				<img src="/dist/image/home/shopping/xuanzhong.png" />
			</p>
		</div>
		<button class="payNow" @click="payNow()"></button>
		<v-alert v-if="alertshow" context=""></v-alert>
	</div>
</template>

<script>
	import Store from "../store/store.js";
	import Header from "../order/header.vue";
	import Alert from "../alert.vue";
	import config from "../config/config.js";
	export default{
		components:{
			"v-header":Header,
			"v-alert": Alert
		},
		data(){
			return{
				money:0,
				orderid:"",
				alertshow: false
			}
		},
		methods:{
			payNow(){
				axios.post("/ec_pay/pay",{
					type:3,
					orderid: this.orderid,
					total_amount: this.money
				},config).then(res => {
					var that = this;
				    WeixinJSBridge.invoke('getBrandWCPayRequest', {
				           "appId": res.data.data.appId,     //     
				           "timeStamp": res.data.data.timeStamp,//1970     
				           "nonceStr": res.data.data.nonceStr, //     
				           "package": res.data.data.package,     
				           "signType":"MD5",         //
				           "paySign": res.data.data.sign // 
				        },
				        function(res){
				            if(res.err_msg == "get_brand_wcpay_request:ok" ) {
				           		that.$parent.show = false;
				            }else{
				            	that.alertshow = true;
				            	setTimeout(() => {
				            		that.alertshow = false;
				            	},1000)
				            }
				        }
				    );
				})
			}
		},
		activated(){
			config.headers.userid = sessionStorage.getItem("userid");
			config.headers.usertoken = sessionStorage.getItem("usertoken");
			this.money = Store.getState().weixin.money;
			this.orderid = Store.getState().weixin.orderid;
		}
	}
</script>

<style>
</style>


