<template>
	<div id="refund" :style="slide">
		<v-header></v-header>
		<div class="fruit">
			<img v-lazy="item.img" class="detail-img" />
			<div class="detail">
				<div class="title">
					<span>{{item.name}}</span>
					<span>{{item.mbn_details}}</span>
				</div>
				<div class="weight">
					<span></span>
					<span>{{item.details}}</span>
				</div>
				<div class="send">
					<img src="/dist/image/home/subhome/send.png"/>
					<img src="/dist/image/home/subhome/all.png"/>
				</div>
				<div class="price">
					<span>{{item.fin_money}}</span>
				</div>
			</div>
		</div>
		<div class="reason">
			<p @click="choose()">
				<span></span>
				<img src="/dist/image/address/more.png" />
				<span></span>
			</p>
			<p>{{reason}}</p>
		</div>
		<div class="refundMoney">
			<span></span>
			<input type="number" placeholder="" v-model="money" />
		</div>
		<div class="max">
			{{item.fin_money}}0.00
		</div>
		<div class="refundState">
			<span></span><input type="text" placeholder="" v-model="state" />
		</div>
		<!--<div class="img-wrap">
			<p></p>
			<div class="addImg">
				<img src="/dist/image/talkingnow/camera.png" />
				<span></span>
			</div>
		</div>-->
		<button class="submit" @click="submit()"></button>
		<div class="reason-wrap" :style="{height:height + 'px'}" v-if="show">
			<div class="bottom" :style="anima">
				<p></p>
				<label @click="change('')">
					<span></span>
					<input type="radio" value="" name="reason"/>
				</label>
				<label @click="change('/')">
					<span>/</span>
					<input type="radio" value="/" name="reason"/>
				</label>
				<label @click="change('')">
					<span></span>
					<input type="radio" value="" name="reason"/>
				</label>
				<label @click="change('')">
					<span></span>
					<input type="radio" value="" name="reason"/>
				</label>
				<button class="off" @click="off()"></button>
			</div>
		</div>
		<v-alert v-if="alertshow" :context="context"></v-alert>
	</div>
</template>

<script>
	import Header from "./header.vue";
	import Alert from "../alert.vue";
	import config from "../config/config.js";
	export default{
		components:{
			"v-header":Header,
			"v-alert":Alert
		},
		data(){
			return {
				height: document.body.clientHeight,
				check:[false,false,false,false],
				show:false,
				reason:"",
				anima:{},
				state:'',
				item:{},
				height: document.body.offsetHeight,
				slide:{},
				alertshow:false,
				context:"",
				money:''
			}
		},
		methods:{
			choose(){
				this.show = true;
				
				this.anima = {
					animation: "myfirst .3s"
				}
			},
			off(){
				this.anima = {
					animation: "mylast .3s"
				}
				setTimeout(()=>{
					
					this.show = false;
				},300)
			},
			change(str){
				this.reason = str;
			},
			submit(){
				if(this.reason == ""){
					this.context = "";
					this.alertshow = true;
					setTimeout(() => {
						this.alertshow = false;
					},1000)
					return;
				}
				if(this.money == ""){
					this.context = "";
					this.alertshow = true;
					setTimeout(() => {
						this.alertshow = false;
					},1000)
					return;
				}
				if(parseFloat(this.money) > parseFloat(this.item.fin_money)){
					this.context = "";
					this.alertshow = true;
					setTimeout(() => {
						this.alertshow = false;
					},1000)
					return;
				}
				axios.post("/ec_pay/backmoney",{
					orderid: this.item.orderid,
					type:1,
					money: this.money,
					backReason: this.reason,
					other: this.state,
					file:''
				},config).then(res => {
					if(res.data.code == 0){
						this.context = "";
						this.alertshow = true;
						setTimeout(() => {
							this.alertshow = false;
						},1000)
						this.$router.go(-1);
						sessionStorage.setItem("index",5);
					}else{
						this.context = ",";
						this.alertshow = true;
						setTimeout(() => {
							this.alertshow = false;
						},1000)
					}
				})
			}
		},
		activated(){
			config.headers.userid = sessionStorage.getItem("userid");
			config.headers.usertoken = sessionStorage.getItem("usertoken");
			this.slide = {
				height:this.height + "px"
			}
			this.item = JSON.parse(this.$route.params.data);
			window.onresize = function(){
				var h = document.body.offsetHeight;
				if(h < that.height){
					that.slide = {
						position:"absolute",
						bottom:0,
						left:0,
						height:that.height + "px"
					}
				}else{
					that.slide = {
						height:that.height + "px"
					};
				}
			}
			wx.hideMenuItems({
			  menuList: ["menuItem:copyUrl","menuItem:readMode","menuItem:openWithQQBrowser","menuItem:openWithSafari","menuItem:share:qq","menuItem:share:weiboApp","menuItem:favorite","menuItem:share:facebook","menuItem:share:QZone"] // menu3
			});
		}
	}
</script>

<style>
</style>


