<template>
	<div id="orderDetail">
		<v-header></v-header>
		<div class="status">
			<img src="/dist/image/orderdetail/photo.png" />
			<p>
				<span>{{detail.status | status}}</span>
				<span></span>
			</p>
			
		</div>
		<div id="yes" v-if="detail.type == 1">
			<div class="name">
				<span>{{detail.reName}}</span>
				<span class="phone">{{detail.rePhone}}</span>
			</div>
			<div class="address">
				{{detail.reAddress}}
			</div>
		</div>
		<img src="/dist/image/order/border.png" style="width: 100%;display: block;" v-if="detail.type == 1"/>
		<div class="fruit" v-if="detail.type == 1">
			<img v-lazy="detail.img" class="detail-img" />
			<div class="detail">
				<div class="title">
					<span>{{detail.name}}</span>
					<span>{{detail.mbn_details}}</span>
				</div>
				<div class="weight">
					<span></span>
					<span>{{detail.details}}</span>
				</div>
				<div class="send">
					<img src="/dist/image/home/subhome/send.png"/>
					<img src="/dist/image/home/subhome/all.png"/>
				</div>
				<div class="price">
					<span>{{(detail.fin_money / detail.buy_num).toFixed(2)}}</span>
					<span>X{{detail.buy_num}}</span>
				</div>
			</div>
		</div>
		<div class="pick" v-if="detail.type == 2">
			<img v-lazy="detail.img" class="single-img"/>
			<div class="single-detail">
				<div class="title">{{detail.name}}</div>
				<div class="address">
					<img src="/dist/image/order/position.png" />
					<span>{{detail.address}}</span>
				</div>
				<div class="price">
					<span>{{detail.fin_money}}</span>
				</div>
			</div>
		</div>
		<div class="finMoney">
			<p>
				<span></span>
				<span>0.00</span>
			</p>
			<p>
				<span></span>
				<span>0.00</span>
			</p>
			<p>{{detail.fin_money}}</p>
			<p><a>{{detail.fin_money}}</a></p>
		</div>
		<div class="orderid">
			<p>
				{{detail.orderid}}
			</p>
			<p v-if="detail.pay_time != ''">
				{{detail.pay_time}}
			</p>
			<p v-if="detail.push_time != ''">
				{{detail.push_time}}
			</p>
		</div>
	</div>
</template>

<script>
	import Header from "./header.vue";
	export default{
		components:{
			"v-header":Header
		},
		data(){
			return{
				detail:this.$route.params.detail
			}
		},
		mounted(){
			this.detail = JSON.parse(this.$route.params.detail);
		},
		activated(){
			this.detail = JSON.parse(this.$route.params.detail);
			wx.hideMenuItems({
			  menuList: ["menuItem:copyUrl","menuItem:readMode","menuItem:openWithQQBrowser","menuItem:openWithSafari","menuItem:share:qq","menuItem:share:weiboApp","menuItem:favorite","menuItem:share:facebook","menuItem:share:QZone"] // menu3
			});
		},
		filters:{
			status: function(val){
				val = parseInt(val);
				switch(val){
					case -1:
						return "";
					case 0:
						return "";
					case 1:
						return "";
					case 2:
						return "";
					case 3:
						return "";
					case 4:
						return "";
					case 5:
						return "";
					case 6:
						return "";
					case 7:
						return "";
					case 8:
						return "";
				}
			}
		}
	}
</script>

<style>
</style>


